	module.exports = {"data":{"markdownRemark":{"html":"<p>The strict inequality operator (<code>!==</code>) is the opposite of the strict equality operator. It means “Strictly Not Equal” and returns <code>false</code> where strict equality would return <code>true</code> and vice versa. Strict inequality will not convert data types.</p>\n<h2>Examples</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>3 !== 3   // false\n3 !== '3' // true\n4 !== 3   // true</code></pre>\n      </div>","fields":{"slug":"/certificates/comparison-with-the-strict-inequality-operator/"},"frontmatter":{"title":"Comparison with the Strict Inequality Operator"}}},"pathContext":{"slug":"/certificates/comparison-with-the-strict-inequality-operator/"}}

